import React, { useState } from "react";
import { Navbar, Container, Nav } from "react-bootstrap";
import { Link } from "react-scroll";
import styled from "styled-components";

const NavBar = styled(Navbar)`
	height: 90px;
	border: none;
	background: rgba(255, 255, 255, 0.3);
	box-shadow: 0 4px 18px 0 rgba(255, 255, 255, 0.17);
	backdrop-filter: blur(1.5px);
	-webkit-backdrop-filter: blur(1.5px);
	border-radius: 10px;
`;

const LinkStyled = styled(Link)`
	text-decoration: none;
	cursor: pointer;
	width: 125px;
	font-size: 100%;
	margin-right: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 15px;
	color: white;

	&:hover {
		background-color: gold;
	}
	@media (max-width: 768px) {
		display: none;
	}
`;

const Title = styled.h3`
	color: black;
	font-weight: bold;
	background-color: white;
	padding: 10px;
	cursor: pointer;
	border-radius: 15px 0 15px 0;
	border-bottom: 2px solid black;
`;

export default function Header() {
	const [navbarbg, setNavbarBg] = useState(false);
	const changeBackground = () => {
		if (window.scrollY >= 80) {
			setNavbarBg(true);
		} else {
			setNavbarBg(false);
		}
	};

	window.addEventListener("scroll", changeBackground);
	return (
		<NavBar
			className={
				navbarbg
					? "bg-transparent w-100"
					: "navbar navbar-expand-lg bg-black w-100"
			}
			fixed="top"
		>
			<Container className="d-flex justify-content-between" >
				<Link  to="intro-scroll" smooth={true} duration={1000} offset={-300}>
					<Title >Shacosu.js</Title>
				</Link>
				<Nav className=" ">
					<LinkStyled
						to="about-scroll"
						smooth={true}
						duration={1000}
						offset={-300}
					>
						<i className="fas fa-info-circle mx-1"></i> Sobre Mi
					</LinkStyled>

					<LinkStyled
						to="proyect-scroll"
						smooth={true}
						offset={-220}
						duration={1000}
					>
						<i className="fas fa-bookmark mx-1"></i> Proyectos
					</LinkStyled>
					<LinkStyled
						to="contact-scroll"
						smooth={true}
						offset={-250}
						duration={1000}
						className="border bg-success border-dark p-1 text-dark"
					>
						<i className="fas fa-envelope mx-1 py-2"></i> Contacto
					</LinkStyled>
				</Nav>
			</Container>
		</NavBar>
	);
}
