import React from "react";
import { Container } from "react-bootstrap";
import { projects } from "../proyects";

import styled from "styled-components";

const Img = styled.img`
	width: 250px;
	height: 250px;
	margin: 0;
	padding: 0;
	cursor: pointer;
	border-radius: 20px 0 20px 0;
	border: 1px solid white;

	&:hover {
		opacity: 0.4;
	}
`;

const LinkStyled = styled.a`
	width: 250px;
	padding: 0;
`;

export default function Proyect() {
	return (
		<div className="d-flex flex-wrap  h-100  w-75" id="proyect-scroll">
			{projects.map((project, i) => (
				<div className="text-white w-25" key={i}>
					<div className="w-full w-full">
						<h3 className="text-white">{project.title}</h3>
						<LinkStyled
							href={project.url}
							target="_blank"
							rel="noopener noreferrer"
						>
							<Img src={project.img} alt={project.title} />
						</LinkStyled>
						<p className="mt-2">
							<b className="border-bottom ">Descripcion: </b>{" "}
							<span className="fw-bolder text-wrap">{project.description}</span>
						</p>
					</div>
				</div>
			))}
		</div>
	);
}
